import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Racialbias(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/racial_bias.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[-5.08, -2.15, 3.87]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere176.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere176_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere176_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Racialbias;
useGLTF.preload("/glossarySelected/racial_bias.glb");
